import { useState } from 'react';

import Tabs, { TabsProps } from '@/components/tabs';
import SystemMsg from './systemMessage';
import UserMsg from './userMessage';
import UserMsgApply from './userMessageApply';

import MyPagination from '@/components/pagination';
import styles from './index.module.less';


const MyMessage = () => {
    const tabs: TabsProps['options'] = [
        {
            label: '系统消息',
            value: 1,
        },
        {
            label: '用户留言',
            value: 2,
        },
    ];
    const [current, setCurrent] = useState<TabsProps['current']>(tabs[0].value);
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);

    const [msgApply, setMsgApply] = useState<boolean>(false);
    const [_msgId, setMsgId] = useState<string>('');

    const handleUserMsg = (val: string) => {
        setMsgId(val);
        setMsgApply(true);
    }

    return (
        <div className={styles.myPost}>
            {
                msgApply && (
                    <div className={styles.main}>
                        <UserMsgApply onClose={() => setMsgApply(false)} />
                    </div>
                )
            }
            {
                !msgApply && (
                    <>
                        <div className={styles.main}>
                            <Tabs options={tabs} current={current} onChange={setCurrent} />
                            <div className={styles.con}>
                                {
                                    current === 1 && [1,2,3,4,5].map((_item, index) => <SystemMsg key={index} />)
                                }
                                {
                                    current === 2 && [1,2,3,4,5].map((_item, index) => <UserMsg key={index} onChange={handleUserMsg} />)
                                }
                            </div>
                        </div>
                        <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
                    </>
                )
            }
            
        </div>
    );
}

export default MyMessage;