import { iconHot } from '@/assets/img';
import styles from './index.module.less';


const HomeHotPlate = () => {

    return (
        <div className={styles.homeHotPlate}>
            <p className={styles.h2}>热门板块</p>
            <div className={styles.block}>
                {
                    [1, 2, 3, 4].map(item => (

                        <div className={styles.item} key={item}>
                            <div className={styles.top}>
                                <img className={styles.icon} src={iconHot} alt="" />
                                <span className={styles.title}>#大型PLC</span>
                            </div>

                            <div className={styles.btm}>
                                <span>阅读量：2344</span>
                                <span>讨论：2344</span>
                            </div>
                        </div>

                    ))

                }
            </div>

        </div>
    );
};

export default HomeHotPlate;