
import { useState, useEffect, useRef } from 'react';
import { iconL } from '@/assets/img';
import { formatDate } from '@/utils';
import cs from 'classnames';
import styles from './index.module.less';

interface UserMsgApplyProps {
    onClose: () => void;
}
interface IMessageItem {
    id: string;
    avatar: string;
    date: string;
    text: string;
}

const UserMsgApply = (props: UserMsgApplyProps) => {
    const { onClose } = props;
    const [input, setInput] = useState<string>('');
    const context = useRef<HTMLDivElement | null>(null);
    // 消息交互数据(根据store里的user判断数据中是我说的还是别人说的)
    const userInfo = {
        id: '1',
        avatar: '',
    };
    const [list, setList] = useState<IMessageItem[]>([]);

    const handleSend = () => {
        if (!input) return;
        setList(v => ([...v, {
            id: userInfo.id,
            avatar: userInfo.avatar,
            date: formatDate(new Date()),
            text: input
        }]))
        setInput('');
    }

    useEffect(() => {
        setList([
            {
                id: '2',
                avatar: '',
                date: '2022-09-09  15:25',
                text: '开启平行视界模式下，easygo.json，指定了特定activity默认全屏显示，但是此activity还是自动分屏了，怎么解决?'
            },
            {
                id: '1',
                avatar: '',
                date: '2022-09-09  15:25',
                text: 'w哈哈'
            },
            {
                id: '2',
                avatar: '',
                date: '2022-09-09  15:25',
                text: '？？？？'
            },
        ]);
    }, []);
    useEffect(() => {
        const dom = context.current;
        if (dom) dom.scrollTop = dom?.scrollHeight || 0; 
    }, [list])

    return (
        <div className={styles.userMsgApply}>
            <div className={styles.head}>
                <span>尘ppfeihen</span>
                <div className={styles.back} onClick={onClose}>
                    <img src={iconL} alt="" />
                    <span>返回</span>
                </div>
            </div>
            <div className={styles.con} ref={context}>
                {
                    list.map((item, index) => (
                        <div className={cs(styles.item, item.id === userInfo.id && styles.rtl)} key={index}>
                            <img className={styles.avatar} src={item.avatar} alt="" />
                            <div>
                                <div className={styles.date}><span>{item.date}</span></div>
                                <div className={styles.msg}>
                                    {item.text}
                                </div>
                            </div>
                        </div>
                    ))
                }
                
            </div>
            <div className={styles.footer}>
                <input type="text" placeholder='请输入' value={input} onChange={(v) => setInput(v.target.value)} />
                <div className={styles.btn} onClick={handleSend}>发送</div>
            </div>
        </div>
    );
}

export default UserMsgApply;