import { useState } from 'react';
import { iconWarn } from '@/assets/img';
import Level from '@/components/level';
import Tabs, { TabsProps } from '@/components/tabs';
import Task from './task';
import Mingxi from './mingxi';
import Shop from './shop';
import LevelInfo from './levelInfo';
import cs from 'classnames';
import styles from './index.module.less';


const MyIntegral = () => {
    const mockPro = 0.3; //模拟通过积分处理后的进度百分比
    const progressPoint = [
        {
            label: '等级1',
            value: 0,
        },
        {
            label: '等级2',
            value: 0.25,
        },
        {
            label: '等级3',
            value: 0.5,
        },
        {
            label: '等级4',
            value: 0.75,
        },
        {
            label: '等级5',
            value: 1,
        }
    ];

    const tabs: TabsProps['options'] = [
        {
            label: '积分任务',
            value: 1,
        },
        {
            label: '积分明细',
            value: 2,
        },
        {
            label: '积分兑换',
            value: 3,
        },
    ];
    const [current, setCurrent] = useState<TabsProps['current']>(tabs[0].value);

    const [levelInfoVisible, setLevelInfoVisible] = useState<boolean>(false);

    return (
        <div className={styles.myIntegral}>
            {levelInfoVisible ? <LevelInfo onClose={() => setLevelInfoVisible(false)} /> : (
                <>
                    <div className={styles.integralWrap}>
                        <div className={styles.t}>
                            <div className={styles.l}>我的等级：<Level /></div>
                            <div className={styles.r} onClick={() => setLevelInfoVisible(true)}>
                                <span>等级说明</span>
                                <img src={iconWarn} alt="" />
                            </div>
                        </div>
                        <div className={styles.desc}>
                            我的累积积分：<span>1220</span>，还差1780积分升级
                        </div>
                        <div className={styles.progressWrap}>
                            <div className={styles.progress}>
                                <div className={styles.progressInner} style={{ width: `${mockPro * 100}%` }}></div>
                            </div>
                            <ul className={styles.pointWrap}>
                                {
                                    progressPoint.map((item, index) => (
                                        <li key={index} className={cs(mockPro >= item.value && styles.light)}>
                                            <span className={styles.icon}></span>
                                            <span>{item.label}</span>
                                        </li>
                                    ))
                                }
                            </ul>
                        </div>
                    </div>
                    <div className={styles.main}>
                        <Tabs options={tabs} current={current} onChange={setCurrent} />
                        {/* 积分任务 */}
                        {
                            current === 1 && <Task />
                        }
                        {/* 积分明细 */}
                        {
                            current === 2 && <Mingxi />
                        }
                        {/* 积分商店 */}
                        {
                            current === 3 && <Shop />
                        }
                    </div>
                </>
            )}

        </div>
    )
}

export default MyIntegral;