
import styles from './index.module.less';
import Paragraph from 'antd/es/typography/Paragraph';

const HomeHotActivity = () => {

    return (
        <div className={styles.homeHotActivity}>
            <p className={styles.h2}>热门活动</p>
            <div className={styles.block}>
                {
                    [1, 2].map(item => (

                        <div className={styles.item} key={item}>
                            <div className={styles.top}>
                                <img className={styles.cover} src="" alt="" />
                            </div>

                            <div className={styles.btm}>
                                <Paragraph ellipsis={{ rows: 1, tooltip: true}} className={styles.title}>持续创新 颠覆传统 | “中控SCADAD持续创新 颠覆传统 | “中控SCADAD</Paragraph>
                            </div>
                        </div>

                    ))

                }
            </div>

        </div>
    );
};

export default HomeHotActivity;