
// import More from '@/components/more';
import { iconPrice, iconAdd } from '@/assets/img';
import Level from '@/components/level';
import InfoList from '@/components/infoList';
import Paragraph from 'antd/es/typography/Paragraph';
import styles from './index.module.less';


const PostUserCard = () => {

    return (
        <div className={styles.homeUserCard}>
            <div className={styles.top}>
                <img className={styles.avatar} src="" alt="" />
                <div>
                    <div className='mb-4'>
                        <span className={styles.userName}>张三三</span>
                        <Level />
                    </div>
                    <div>
                        <img className={styles.iconPrice} src={iconPrice} alt="" />
                        <span className={styles.jifen}>1220积分</span>
                    </div>
                </div>
            </div>
            <Paragraph ellipsis={{ rows: 2, tooltip: true}} className={styles.desc}>现任F轻系统架构师，10年的Net领域经验，欢迎留言！</Paragraph>
            <InfoList />
            <div className={styles.btnWrap}>
                <div className={styles.apply}>私信</div>
                <div className={styles.follow}>
                    <img src={iconAdd} alt="" />
                    <span>关注</span>
                </div>
            </div>
        </div>
    );
};

export default PostUserCard;