
import { iconMenu } from '@/assets/img';
import styles from './index.module.less';


const SideMenu = () => {
    const list = [
        {
            title: '技术讨论',
            icon: iconMenu,
            children: [
                {
                    title: '二级分类一',
                },
                {
                    title: '二级分类二',
                }
            ],
        },
        {
            title: '行业知识及案例',
            icon: iconMenu,
            children: [
                {
                    title: '二级分类一',
                },
                {
                    title: '二级分类二',
                }
            ],
        },
        {
            title: '工程师故事',
            icon: iconMenu,
            children: [
                {
                    title: '二级分类一',
                },
                {
                    title: '二级分类二',
                }
            ],
        },
        {
            title: '大咖专栏',
            icon: iconMenu,
            children: [
                {
                    title: '专家一',
                },
                {
                    title: '专家二',
                }
            ],
        },
        {
            title: '培训学习',
            icon: iconMenu,
            children: [
                {
                    title: '二级分类一',
                },
                {
                    title: '二级分类二',
                }
            ],
        },
    ];
    return (
        <div className={styles.sideMenu}>
            {
                list.map((item, index) => (
                    <div key={index} className={styles.l1}>
                        <div className={styles.l1Title}>
                            <img src={item.icon} alt="" />
                            <span>{item.title}</span>
                        </div>
                        <div className={styles.l2}>
                            {
                                (item.children || []).map((it, ind) => (
                                    <div key={ind} className={styles.l2Title}>{it.title}</div>
                                ))
                            }
                        </div>
                    </div>
                ))
            }
        </div>
    );
};

export default SideMenu;