
import More from '@/components/more';
import { iconNotice } from '@/assets/img';
import styles from './index.module.less';


const HomeNotice = () => {

    return (
        <div className={styles.homeNotice}>
            <div className={styles.top}>
                <span>社区公告</span>
                <More />
            </div>
            <div className={styles.block}>
                <img className={styles.icon} src={iconNotice} alt="" />
                <span>中控论坛现已经升级完毕并更换新的域名重新开放，欢迎大家探索使用，还有超多积分奖励等你来拿！</span>
            </div>
        </div>
    );
};

export default HomeNotice;