import { useState } from 'react';
import { iconPrice } from '@/assets/img';
import MyPagination from '@/components/pagination';
import { IntegralType } from '@/interface';
import { IntegralTypeIconMap } from '@/constant';
import styles from './index.module.less';

export interface MingxiItemProps {
    type: IntegralType;
    title: string;
    count: number;
    date: string;
}

const Mingxi = () => {
    const taskList: MingxiItemProps[] = [
        {
            type: IntegralType.PUTONG,
            title: "发普通贴",
            count: 200,
            date: "2022-03-22  15:00",
        },
        {
            type: IntegralType.QIUZHU,
            title: "发求助帖",
            count: 200,
            date: "2022-03-22  15:00",
        },
        {
            type: IntegralType.PINGLUN,
            title: "发评论",
            count: 200,
            date: "2022-03-22  15:00",
        },
        {
            type: IntegralType.ZHUCE,
            title: "新用户注册",
            count: 2,
            date: "2022-03-22  15:00",
        },
    ];
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);
    return (
        <div className={styles.listWrap}>
            {
                taskList.map(({ type, title, count, date }, index) => (
                    <div className={styles.taskItem} key={index}>
                        <img className={styles.icon} src={IntegralTypeIconMap[type]} alt="" />
                        <p className={styles.title}>{title}</p>
                        <div className={styles.count}>
                            <img src={iconPrice} alt="" />
                            <span>+{count}积分</span>
                        </div>
                        <span className={styles.date}>{date}</span>
                    </div>
                ))
            }
            <div className={styles.fenye}>
                <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
            </div>
        </div>
    )

}

export default Mingxi;