import { iconAdd } from '@/assets/img';
import styles from './index.module.less';


const HomeDalao = () => {

    return (
        <div className={styles.homeDalao}>
            <p className={styles.h2}>优秀大佬</p>
            <div className={styles.block}>
                {
                    [1, 2, 3, 4, 5].map(item => (

                        <div className={styles.item} key={item}>
                            <img className={styles.avatar} src="" alt="" />
                            <div className={styles.r}>
                                <div className={styles.t}>
                                    <span className={styles.name}>尘ppfeihen</span>
                                    {
                                        true ? (
                                            <div className={styles.follow}>
                                                <img src={iconAdd} alt="" />
                                                <span>关注</span>
                                            </div>
                                        ) : (
                                            <div className={styles.followed}>已关注</div>
                                        )
                                    }
                                </div>
                                <div className={styles.b}>
                                    <span>阅读：34345</span>
                                    <span>讨论：34345</span>
                                </div>
                            </div>
                        </div>

                    ))

                }
            </div>

        </div>
    );
};

export default HomeDalao;