import { useCallback, useMemo, useRef } from 'react';
import { FileZipOutlined } from '@ant-design/icons';
import ReactQuill, { ReactQuillProps } from 'react-quill';
import Quill from 'quill';
import 'react-quill/dist/quill.snow.css';
import styles from './index.module.less';


// interface QuillEditorProps {
//     value?: string;
//     onChange?: (val: string) => void;
// }
interface QuillWithHistory extends Quill {
    history: {
      undo: () => void;
      redo: () => void;
    };
  }
  
  interface IReactQuill extends ReactQuill {
    getEditor(): QuillWithHistory;
  }

const QuillEditor = (props: ReactQuillProps) => {
    // const { value, onChange } = props;

    const quillRef = useRef<IReactQuill | null>(null);

    const insertZip = () => {
        // 点击自定义图标后的回调
        console.log(1111)
        const input = document.createElement('input');
        input.setAttribute('type', 'file');
        input.setAttribute('accept', '.zip');
        input.click();
        input.onchange = async () => {
            const file = input?.files?.[0];
            console.log('file', file);
            const quill = quillRef.current?.getEditor();
            const range = quill?.getSelection();
            if (range) {
                quill?.insertEmbed(range.index, 'image', 'https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg');
                quill?.insertText(range.index, "Link Text", 'link', "https://example.com");
            }
        }

    }
    const insertImage = () => {
        console.log('image', quillRef.current?.getEditor());
    }
    const insertVideo = () => {
        console.log('video');
    }

    const CustomToolbar = useCallback(() => (
        <div id="toolbar">
            <button className="ql-clean"></button>
            <button className="ql-bold"></button>
            <button className="ql-italic"></button>
            <button className="ql-underline"></button>
            <button className="ql-strike"></button>
            <select className="ql-color">
                <option value="rgb(0, 0, 0)">Black</option>
                <option value="rgb(255, 0, 0)">Red</option>
                <option value="rgb(0, 255, 0)">Green</option>
                <option value="rgb(0, 0, 255)">Blue</option>
            </select>
            <select className="ql-background" style={{ borderRight: '1px solid #D6DBE5' }}>
                <option value="rgb(255, 255, 255)">White</option>
                <option value="rgb(0, 0, 0)">Black</option>
                <option value="rgb(255, 0, 0)">Red</option>
                <option value="rgb(0, 255, 0)">Green</option>
            </select>

            <button className="ql-align" value=""></button>
            <button className="ql-align" value="center"></button>
            <button className="ql-align" value="right" style={{ borderRight: '1px solid #D6DBE5' }}></button>

            <button className="ql-code-block"></button>
            <button className="ql-link"></button>
            <button className="ql-image"></button>
            <button className="ql-video"></button>


            <button className="ql-insertZip" style={{ borderRight: '1px solid #D6DBE5' }}>
                <FileZipOutlined />
            </button>

            <button className="ql-undo" onClick={() => {quillRef.current?.getEditor().history.undo()}}>back</button>
            <button className="ql-redo" onClick={() => {quillRef.current?.getEditor().history.redo()}}>go</button>
        </div>
    ), []);

    // 直接声明会有显示问题
    const modules = useMemo(() => ({
        toolbar: {
            container: '#toolbar',
            handlers: {
                insertZip: insertZip,
                image: insertImage,
                video: insertVideo
            },
        },
    }), []);

    return (
        <div className={styles.editerWrap}>
            <CustomToolbar />
            <ReactQuill className={styles.editer} ref={quillRef} theme="snow" modules={modules} {...props} />
        </div>
    )
};

export default QuillEditor;