
import { useState } from 'react';
import cs from 'classnames';
import styles from './index.module.less';

interface PhoneCodeProps {
    value: string;
    onchange: (val: string) => void;
    className?: string
}

const PhoneCode = (props: PhoneCodeProps) => {
    const { value, onchange, className } = props;
    const defaultTimer = '发送验证码';
    const [timer, setTimer] = useState<string>(defaultTimer);

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        onchange?.(e.target.value);
    }
    const fetchCode = () => {
        // 请求获取code api
    }
    const handleTimer = () => {
        if (timer !== defaultTimer) return;
        let count = 60;
        setTimer(count + 's');
        fetchCode();
        const interval = setInterval(() => {
            if (count <= 0) {
                setTimer(defaultTimer);
                clearInterval(interval);
            } else {
                count--;
                setTimer(count + 's');
            }
        }, 1000);
    }

    return (
        <div className={cs(styles.codeWrap, className)}>
            <input className={styles.input} type="text" placeholder='请输入验证码' value={value} onChange={handleChange} />
            <div className={styles.btn} onClick={handleTimer}>{timer}</div>
        </div>
    )
}

export default PhoneCode