// ProtectedRoute.js
import React from 'react';
import { Route, Redirect, RouterProps } from 'react-router-dom';
import { RouteConfigComponentProps } from 'react-router-config';

// 此处省略了 isAuthenticated 检查逻辑
// 请根据实际情况返回一个表示用户认证状态的布尔值
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const isAuthenticated = (props: RouteConfigComponentProps<any>) => {
  // ...
  console.log('路由切换', props)
  const { pathname } = props.location;
  const token = undefined;
  const white_page = ['/UserCenter'];
  return !(white_page.includes(pathname) && !token);
};

interface ProtectedRouteProps extends Omit<RouterProps, 'history'> {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    component: React.ComponentType<any>;
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      isAuthenticated(props) ? (
        <Component {...props} />
      ) : (
        <Redirect to="/login" />
      )
    }
  />
);

export default ProtectedRoute;