import { useState } from 'react';
import cs from 'classnames';
import { iconPost1, iconPost2, iconPost3, iconPost4 } from '@/assets/img';
import PostItem from '@/components/postItem';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';

const PostList = () => {
    const tabs = [
        {
            label: '普通帖',
            value: 1,
            icon: iconPost1,
        },
        {
            label: '求助帖',
            value: 2,
            icon: iconPost2,
        },
        {
            label: '故事帖',
            value: 3,
            icon: iconPost3,
        },
        {
            label: '分享帖',
            value: 4,
            icon: iconPost4,
        },
    ];
    const [selectedTab, setSelectedTab] = useState(tabs[0].value);
    const types = [
        {
            label: '精华',
            value: 1,
        },
        {
            label: '热门',
            value: 2,
        },
        {
            label: '新发布',
            value: 3,
        },
        {
            label: '新回复',
            value: 4,
        },
    ];
    const [selectedType, setSelectedType] = useState(types[0].value);

    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);

    return (
        <div className={styles.homePostList}>
            <div className={styles.header}>
                <div className={styles.tabs}>
                    {
                        tabs.map(item => (
                            <div
                                className={cs(styles.pane, item.value === selectedTab && styles.selectedTab)}
                                key={item.value}
                                onClick={() => setSelectedTab(item.value)}
                            >
                                <img className={styles.icon} src={item.icon} alt="" />
                                <span>{item.label}</span>
                            </div>
                        ))
                    }
                </div>
                <div className={styles.r}>
                    {
                        types.map(item => <span
                            key={item.value}
                            className={cs(item.value === selectedType && styles.selectedType)}
                            onClick={() => setSelectedType(item.value)}
                        >
                            {item.label}
                        </span>)
                    }
                </div>
            </div>
            <div className={styles.postList}>
                {
                    [1, 2, 3, 4].map(item => (
                        <PostItem key={item} />
                    ))
                }
            </div>
            {/* 分页 */}
            <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v }))} total={pager.total} />
        </div>
    );
}

export default PostList;