

import { useHistory } from 'react-router-dom';
import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import { iconL, iconEye } from '@/assets/img';
import styles from './index.module.less';

const ActivityDetail = () => {
    const history = useHistory();
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '活动列表',
            route: '/activity-list',
        },
        {
            name: '活动详情'
        }
    ];
    const onClose = () => history.push('/activity-list');

    return (
        <div className={styles.activityDetail}>
            <BreadCrumb list={crumb} />
            <div className={styles.main}>
                <div className={styles.head}>
                    {/* <span>等级说明</span> */}
                    <div className={styles.back} onClick={onClose}>
                        <img src={iconL} alt="" />
                        <span>返回</span>
                    </div>
                </div>
                <div className={styles.content}>
                    <p className={styles.title}>基于InPlantSCADA的数据采集与转发(协议转换)解决方案</p>
                    <div className={styles.btm}>
                        <span>2022-05-03  15:56</span>
                        <div className={styles.r}>
                            <span>1268</span>
                            <img src={iconEye} alt="" />
                        </div>
                    </div>
                </div>
            </div>
            <div className={styles.footer}>
                <div className={styles.l}>
                    <span className={styles.label}>上一篇</span>
                    <span className={styles.value}>基于InPlantSCADA的数据采集与转发(协议转换)解决方案解决方</span>
                </div>
                <div className={styles.r}>
                    <span className={styles.label}>下一篇</span>
                    <span className={styles.value}>基于InPlantSCADA的数据采集与转发(协议转换)解决方案转发(协议转换)解决方案</span>
                </div>
            </div>
        </div>
    )
    
}

export default ActivityDetail;