
import { iconPrice, code1 } from '@/assets/img';
import Paragraph from 'antd/es/typography/Paragraph';
import styles from './index.module.less';

const Shop = () => {
    
    return (
        <>
            <div className={styles.shopWrap}>
                {
                    [1,2,3,4,5].map((_item, index) => (
                        <div key={index} className={styles.item}>
                            <img className={styles.cover} src="" alt="" />
                            <Paragraph className={styles.title} ellipsis={{ rows: 2, tooltip: true}}>现任F轻系统架构师，10年的Net领域经验，欢迎留言！</Paragraph>
                            <div className={styles.price}>
                                <img src={iconPrice} alt="" />
                                <span>2020</span>
                            </div>
                        </div>
                    ))
                }
            </div>
            <div className={styles.shopFooter}>
                <span>积分兑换请扫描一下二维码添加小助手好友，审核通过后即可领取</span>
                <img src={code1} alt="" />
            </div>
        </>
        
    )

}

export default Shop;