

import { iconUp, iconComment, iconEye, iconLiked } from '@/assets/img';
import styles from './index.module.less';

const PostItem = () => {
    return (
        <div className={styles.postItem}>
            <div className={styles.top}>
                <div className={styles.l}>
                    <span className={styles.icon}>精华</span>
                    <span className={styles.title}>中控仪表天然气门站监控管理系统简介</span>
                </div>
                <img className={styles.iconUp} src={iconUp} alt="" />
            </div>
            <div className={styles.desc}>视频监控画面和数据监测画面，通过LED播放管理系统控制并发送至大屏，以便全面、直观的监测</div>
            <div className={styles.btm}>
                <div className={styles.l}>
                    <img className={styles.avatar} src="" alt="" />
                    <ul className={styles.info}>
                        <li>陈琪</li>
                        <li>2022-05-03 15:56</li>
                        <li>产品分类：web发布</li>
                    </ul>
                </div>
                <ul className={styles.r}>
                    <li>
                        <span>1268</span>
                        <img src={iconEye} alt="" />
                    </li>
                    <li>
                        <span>455</span>
                        <img src={iconComment} alt="" />
                    </li>
                    <li>
                        <span>55</span>
                        <img className='cursor' src={iconLiked} alt="" />
                    </li>
                </ul>
            </div>
        </div>
    );
}

export default PostItem;