


import styles from './index.module.less';

interface UserMsgProps {
    onChange: (id: string) => void;
}

const UserMsg = (props: UserMsgProps) => {
    const { onChange } = props;
    return (
        <div className={styles.userMsg} onClick={() => onChange('111')}>
            <img className={styles.avatar} src="" alt="" />
            <div className={styles.r}>
                <div className={styles.t}>
                    <p className={styles.username}>张三三</p>
                    <p className={styles.date}>2022-09-09  15:25</p>
                </div>
                <div className={styles.b}>
                    <p className={styles.desc}>开启平行视界模式下，easygo.json，指定了特定activity默认全屏显示，但是此activity还是自动分…</p>
                    <div className={styles.dot}>9</div>
                </div>
            </div>
        </div>
    );
}

export default UserMsg;