

import { iconCollected, iconYuantie } from '@/assets/img';
import styles from './index.module.less';

const MyCollectComment = () => {
    return (
        <div className={styles.postItem}>
            <div className={styles.top}>
                <img className={styles.avatar} src="" alt="" />
                <div>
                    <p className={styles.username}>陈琪</p>
                    <p className={styles.date}>2022-05-03  15:56</p>
                </div>
                <img className={styles.icon} src={iconCollected} alt="" />
            </div>
            <div className={styles.desc}>如果这个服务器只用来搭建博客是不是有些浪费了，那么在docker镜像站也有大佬提供了ftp的镜像，如果这个服务器只用来搭建博客是不是有些浪费了，那么在docker镜像站也有大佬提供了ftp的镜像</div>
            <div className={styles.btm}>
                <img className={styles.icon} src={iconYuantie} alt="" />
                <span className={styles.title}>中控仪表天然气门站监控管理系统简介</span>
            </div>
        </div>
    );
}

export default MyCollectComment;