
import { useState } from 'react';
import Level from '@/components/level';
import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import InfoList from '@/components/infoList';
import FollowItem from './components/followItem';
import Tabs, {TabsProps} from '@/components/tabs';
import PostItem from '@/components/postItem';
import CommentItem from '@/components/commentItem';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';


// 别人的主页
const OtherPeople = () => {
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '他人主页'
        }
    ];
    const tabs: TabsProps['options'] = [
        {
            label: 'TA的发帖',
            value: 1,
        },
        {
            label: 'TA的评论',
            value: 2,
        },
    ];
    const [current, setCurrent] = useState<TabsProps['current']>(tabs[0].value);
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);
    return (
        <>
            <div className={styles.baseInfo}>
                <div className={styles.avatar}>
                    <img src="" alt="" />
                    <div className={styles.level}>
                        <Level />
                    </div>
                </div>
                <p className={styles.username}>张三三</p>
                <p className={styles.desc}>现任F轻系统架构师，10年的Net领域经验，欢迎留言！</p>
                <div className={styles.btnWrap}>
                    <div className={styles.apply}>私信</div>
                    <div className={styles.follow}>+ 关注</div>
                </div>
            </div>
            <div className={styles.content}>
                <BreadCrumb list={crumb} />
                <div className={styles.main}>
                    <div className={styles.l}>
                        <div className={styles.top}>
                            <InfoList />
                        </div>
                        <div className={styles.con}>
                            <p className={styles.title}>TA的关注</p>
                            {
                                [1,2,3,4,5].map((_item, index) => <FollowItem key={index} />)
                            }
                        </div>
                    </div>
                    <div className={styles.r}>
                        <div className={styles.rInner}>
                            <Tabs options={tabs} current={current} onChange={setCurrent} />
                            <div className={styles.list}>
                                {
                                    current === 1 && ([1,2,3,4,5,6].map((_item, index) => <PostItem key={index} />))
                                }
                                {
                                    current === 2 && ([1,2,3,4,5,6].map((_item, index) => <CommentItem key={index} />))
                                }
                            </div>
                        </div>
                        {/* 共用一个分页器，切换tabs时重置数据 */}
                        <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
                    </div>
                </div>
            </div>
            
        </>
    );
}

export default OtherPeople;