

import { useHistory } from 'react-router-dom';
import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import { iconL } from '@/assets/img';
import styles from './index.module.less';

const SinglePage = () => {
    const history = useHistory();
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '我要发贴',
            route: '/post-publish',
        },
        {
            name: '中控社区发贴规范'
        }
    ];

    return (
        <div className={styles.singlePage}>
            <BreadCrumb list={crumb} />
            <div className={styles.main}>
                <div className={styles.head}>
                    <div className={styles.back} onClick={() => history.goBack()}>
                        <img src={iconL} alt="" />
                        <span>返回</span>
                    </div>
                </div>
                <div className={styles.content}>
                    <p className={styles.title}>中控社区发帖规范</p>
                    <p className={styles.date}>更新时间：2023-8-25</p>
                    <div>富文本</div>
                </div>
            </div>
        </div>
    );
}

export default SinglePage;