// routes.js
import { Redirect } from 'react-router-dom';
import { RouteConfig } from 'react-router-config';
import ProtectedRoute from './ProtectedRoute';

import Layout from '@/views/layout';
import Home from '@/views/home';
import userCenter from '@/views/userCenter';
import Login from '@/views/login';
import PostCenter from '@/views/postCenter';
import PostDetail from '@/views/postDetail';
import PostPublish from '@/views/postPublish';
import OtherPeople from '@/views/otherPeople';
import BigShots from '@/views/bigShots';
import ActivityList from '@/views/activityList';
import ActivityDetail from '@/views/activityDetail';
import SinglePage from '@/views/singlePage';
import NoticeList from '@/views/noticeList';
// import NotFound from '@/views/notFound';

const routes: RouteConfig[] = [
  {
    path: '/',
    exact: true,
    render: () => <Redirect to="/home" />
  },
  {
    component: Layout,
    routes: [
      {
        path: '/login',
        exact: true,
        component: Login
      },
      {
        path: '/home',
        exact: true,
        component: Home
      },
      {
        path: '/post-center',
        exact: true,
        component: PostCenter
      },
      {
        path: '/post-detail',
        exact: true,
        component: PostDetail
      },
      {
        path: '/post-publish',
        exact: true,
        render: () => <ProtectedRoute component={PostPublish} />
      },
      {
        path: '/other-people',
        exact: true,
        component: OtherPeople
      },
      { 
        path: '/user-center', 
        exact: true, 
        render: () => <ProtectedRoute component={userCenter} />
      },
      {
        path: '/big-shots',
        exact: true,
        component: BigShots
      },
      {
        path: '/activity-list',
        exact: true,
        component: ActivityList
      },
      {
        path: '/activity-detail',
        exact: true,
        component: ActivityDetail
      },
      {
        path: '/single-page',
        exact: true,
        component: SinglePage
      },
      {
        path: '/notice-list',
        exact: true,
        component: NoticeList
      },
    ]
  },

];

export default routes;