import { useState } from 'react';

import Tabs, { TabsProps } from '@/components/tabs';
import PostItem from '@/components/postItem';
import MyCollectComment from '../myCollectComment';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';


const MyCollect = () => {
    const tabs: TabsProps['options'] = [
        {
            label: '收藏的帖子',
            value: 1,
        },
        {
            label: '收藏的评论',
            value: 2,
        },
    ];
    const [current, setCurrent] = useState<TabsProps['current']>(tabs[0].value);
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);
    return (
        <div className={styles.myPost}>
            <div className={styles.main}>
                <Tabs options={tabs} current={current} onChange={setCurrent} />
                <div className={styles.con}>
                    {
                        current === 1 && [1,2,3,4,5].map((_item, index) => <PostItem key={index} />)
                    }
                    {
                        current === 2 && [1,2,3,4,5].map((_item, index) => <MyCollectComment key={index} />)
                    }
                </div>
            </div>
            <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
        </div>
    );
}

export default MyCollect;