

import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import UserCard from './component/userCard';
import SideBar from './component/sideBar';
import BaseInfo from './component/baseInfo';
import MyPost from './component/myPost';
import MyComment from './component/myComment';
import MyCollect from './component/myCollect';
import MyMessage from './component/myMessage';
import MyIntegral from './component/myIntegral';
import MyFollow from './component/myFollow';

import { useLocation  } from 'react-router-dom';
import styles from './index.module.less';

const UserCenter = () => {
    const location = useLocation();
    const us_tab = new URLSearchParams(location.search).get('us_tab') || '5';
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '账号资料'
        }
    ];
    return (
        <div className={styles.userCenter}>
            <BreadCrumb list={crumb} />
            <div className={styles.container}>
                <div className={styles.l}>
                    <UserCard />
                    <SideBar />
                </div>
                <div className={styles.r}>
                    {/* 账号资料 */}
                    {
                        us_tab === '5' && <BaseInfo />
                    }
                    {/* 我的帖子 */}
                    {
                        us_tab === '1' && <MyPost />
                    }
                    {/* 我的评论 */}
                    {
                        us_tab === '2' && <MyComment />
                    }
                    {/* 我的收藏 */}
                    {
                        us_tab === '3' && <MyCollect />
                    }
                    {/* 我的消息 */}
                    {
                        us_tab === '4' && <MyMessage />
                    }
                    {/* 我的积分 */}
                    {
                        us_tab === '6' && <MyIntegral />
                    }
                    {/* 我的关注/粉丝 */}
                    {
                        us_tab === '7' && <MyFollow />
                    }
                </div>
            </div>
        </div>
    );
}
export default UserCenter;