import { useState } from 'react';
import cs from 'classnames';
import PhoneCode from '@/components/phoneCode';
import { useHistory } from 'react-router-dom';
import styles from './index.module.less';

interface ILoginType {
    title: string;
    key: string;
}

const Login = () => {
    const history = useHistory();
    const loginType: ILoginType[] = [
        {
            title: '快捷登录',
            key: '1',
        },
        {
            title: '密码登录',
            key: '2',
        },
    ];
    const [selectLoginType, setSelectLoginType] = useState<string>('1');
    const defaultLoginForm = {
        phone: '',
        code: '',
        pw: ''
    }
    const [loginForm, setLoginForm] = useState(defaultLoginForm);
    const changeLoginForm = (value: string, key: string) => {
        setLoginForm({
            ...loginForm,
            [key]: value
        })
    }

    return (
        <div className={cs(styles.block, styles.login)}>
            <div className={styles.tabs}>
                {
                    loginType.map(item => (
                        <span key={item.key} className={cs(item.key === selectLoginType && styles.actived)} onClick={() => setSelectLoginType(item.key)}>{item.title}</span>
                    ))
                }
            </div>
            {
                selectLoginType === '1' ? (
                    <div className={styles.form}>
                        <input className={styles.input} type="text" placeholder='请输入手机号' value={loginForm.phone} onChange={e => changeLoginForm(e.target.value, 'phone')} />
                        <PhoneCode className={styles.input} value={loginForm.code} onchange={val => changeLoginForm(val, 'code')} />
                    </div>
                ) : (
                    <div className={styles.form}>
                        <input className={styles.input} type="text" placeholder='请输入手机号' value={loginForm.phone} onChange={e => changeLoginForm(e.target.value, 'phone')} />
                        <input className={styles.input} type="text" placeholder='请输入密码' value={loginForm.pw} onChange={e => changeLoginForm(e.target.value, 'pw')} />
                    </div>
                )
            }

            <div className={styles.tipsWrap}>
                <span className={styles.l}>还没账号？<span onClick={() => history.push('/login', {type: 'register'})}>立即注册</span></span>
                <span className={styles.r} onClick={() => history.push('/login', {type: 'find'})}>找回密码</span>
            </div>
            <div className={styles.confirmBtn}>立即登录</div>
        </div>

    );
}

const Register = () => {
    const defaultRegisterForm = {
        phone: '',
        code: '',
        pw: ''
    }
    const [registerForm, setRegisterForm] = useState(defaultRegisterForm);
    const [agree, setAgree] = useState<boolean>(false);


    const changeRegisterForm = (value: string, key: string) => {
        setRegisterForm({
            ...registerForm,
            [key]: value
        })
    }

    return (
        <div className={cs(styles.block, styles.register)}>
            <div className={styles.h2}>注册</div>
            <div className={styles.form}>
                <input className={styles.input} type="text" placeholder='请输入手机号' value={registerForm.phone} onChange={e => changeRegisterForm(e.target.value, 'phone')} />
                <input className={styles.input} type="text" placeholder='请输入登录密码，密码含数字+字母，6位及' value={registerForm.pw} onChange={e => changeRegisterForm(e.target.value, 'pw')} />
                <PhoneCode className={styles.input} value={registerForm.code} onchange={val => changeRegisterForm(val, 'code')} />
            </div>

            <div className={styles.agreeWrap}>
                <span className={cs(styles.agree, agree && styles.checked)} onClick={() => setAgree(v => !v)}></span>
                <span>我已同意</span>
                <span className={styles.col}>《用户注册协议》</span>
                <span>及</span>
                <span className={styles.col}>《隐私政策》</span>
            </div>
            
            <div className={styles.confirmBtn}>立即注册</div>
        </div>

    );
}

const FindPw = () => {
    const defaultFindForm = {
        phone: '',
        code: '',
        pw: '',
        pw_again: ''
    }
    const [findForm, setFindForm] = useState(defaultFindForm);


    const changeFindForm = (value: string, key: string) => {
        setFindForm({
            ...findForm,
            [key]: value
        })
    }

    return (
        <div className={cs(styles.block, styles.find)}>
            <div className={styles.h2}>找回密码</div>
            <div className={styles.form}>
                <input className={styles.input} type="text" placeholder='请输入手机号' value={findForm.phone} onChange={e => changeFindForm(e.target.value, 'phone')} />
                <PhoneCode className={styles.input} value={findForm.code} onchange={val => changeFindForm(val, 'code')} />
                <input className={styles.input} type="text" placeholder='请输入新密码，密码含数字+字母，6位及以' value={findForm.pw} onChange={e => changeFindForm(e.target.value, 'pw')} />
                <input className={styles.input} type="text" placeholder='请输入新密码，密码含数字+字母，6位及以' value={findForm.pw_again} onChange={e => changeFindForm(e.target.value, 'pw_again')} />
            </div>
            <div className={styles.line}></div>
            
            <div className={styles.confirmBtn}>确认</div>
        </div>

    );
}

const LoginLayout = (props: any) => {
    const type = props?.location?.state?.type || 'login';
    return (
        <div className={styles.loginWrap}>
            {
                type === 'login' && <Login />
            }
            {
                type === 'register' && <Register />
            }
            {
                type === 'find' && <FindPw />
            }
        </div>
    );
}

export default LoginLayout;