

import { iconClose } from '@/assets/img';
import styles from './index.module.less';


// 他人评论，我的评论（右上角多个删除功能）
const CommentItem = () => {
    
    return (
        <div className={styles.item}>
            <div className={styles.top}>
                <span className={styles.icon}>精华</span>
                <span className={styles.title}>利用大数据提升“化工生产安全、环保管理水平”——中控仪表园区行之上虞站</span>
                <img className={styles.close} src={iconClose} alt="" />
            </div>
            <div className={styles.content}>
                <span className={styles.label}>TA的评论：</span>
                <span className={styles.value}>视频监控画面和数据监测画面，通过LED播放管理系统控制并发送至大屏，视频监控画面和数据监测画面，通过LED播放管理系统控制并发送至大屏</span>
            </div>
            <span className={styles.date}>2022-05-03</span>
        </div>
    );
}


export default CommentItem;