import { useState } from 'react';

import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import Paragraph from 'antd/es/typography/Paragraph';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';

const ActivityList = () => {
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '活动列表'
        }
    ];
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);
    return (
        <div className={styles.activityList}>
            <BreadCrumb list={crumb} />
            <div className={styles.main}>
                {
                    [1,2,3,4,5].map((_item, index) => (
                        <div className={styles.item} key={index}>
                            <img className={styles.cover} src="" alt="" />
                            <div className={styles.r}>
                                <div>
                                    <p className={styles.title}>基于InPlantSCADA的数据采集与转发(协议转换)解决方案</p>
                                    <Paragraph ellipsis={{ rows: 2}} className={styles.desc}>中控综合监控软件（SCADA）是中控技术自主研发推出的面向大规模分布式工业生产现场应用发推出的面向大规模分布式工业生产的综合监控组态系统软件，为企业提大规模分布式工业生产的综合监控组态系统软件，为企业提供从供从下到业生产现生产的综合监控组态系统软件，为企业提供从供从下到到中控综合监控软件（SCADA）是中控技术自主研发推出的面向大规模分布式工业生产现场应用发推出的面向大规模分布式工业生产的综合监控组态系统软件，为企业提大规模分布式工业生产的综合监控组态系统软件，为企业提供从供从下到业生产现生产的综合监控组态系统软件，为企业提供从供从下到到</Paragraph>
                                </div>
                                <div className={styles.btm}>
                                    <span className={styles.date}>2023-10-09</span>
                                    <span className={styles.look}>查看详情</span>
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div>
            <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
        </div>
    )
    
}

export default ActivityList;