
// import { useState } from 'react';
import { iconUser1, iconUser2, iconUser3, iconUser4, iconUser5, iconR, iconR2 } from '@/assets/img';
import cs from 'classnames';
import { useHistory, useLocation  } from 'react-router-dom';
import styles from './index.module.less';


const SideBar = () => {
    const history = useHistory();
    const location = useLocation();
    const list = [
        {
            label: '我的帖子',
            value: '1',
            icon: iconUser1,
        },
        {
            label: '我的评论',
            value: '2',
            icon: iconUser2,
        },
        {
            label: '我的收藏',
            value: '3',
            icon: iconUser3,
        },
        {
            label: '我的消息',
            value: '4',
            icon: iconUser4,
        },
        {
            label: '账号资料',
            value: '5',
            icon: iconUser5,
        },
    ];

    const actived = new URLSearchParams(location.search).get('us_tab') || '5';
    const handleClick = (val: string) => {
        history.push(`/user-center?us_tab=${val}`)
    }
    return (
        <div className={styles.sideBar}>
            {
                list.map((item, index) => (
                    <div key={index} className={cs(styles.li, item.value === actived && styles.actived)} onClick={() => handleClick(item.value)}>
                        <div className={styles.l}>
                            <img src={item.icon} alt="" />
                            <span>{item.label}</span>
                        </div>
                        <img className={styles.icon} src={item.value === actived ? iconR2 : iconR} alt="" />
                    </div>
                ))
            }
        </div>
    );
}

export default SideBar;