import { useState } from 'react';

import { Radio, Checkbox } from 'antd';
import PhoneModal from './phoneModal';
import PasswordModal from './passwordModal';
import cs from 'classnames';
import styles from './index.module.less';


const BaseInfo = () => {
    const industryList = [
        {
            label: '市政',
            value: '1',
        },
        {
            label: '电力',
            value: '2',
        },
        {
            label: '石油化工',
            value: '3',
        },
        {
            label: '油气长输',
            value: '4',
        },
        {
            label: '医药',
            value: '5',
        },
        {
            label: '智能制造',
            value: '6',
        },
        {
            label: '其他',
            value: '7',
        },
    ];
    const [phoneVisible, setPhoneVisible] = useState<boolean>(false);
    const [pwVisible, setPwVisible] = useState<boolean>(false);

    return (
        <div className={styles.baseInfo}>
            <p className={styles.h2}>账号资料</p>
            <div className={styles.form}>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>头像</span>
                    </div>
                    <div className={styles.value}>
                        <div className={styles.avatarWrap}>
                            <img className={styles.avatar} src="" alt="" />
                            <div className={styles.formBtn}>上传头像</div>
                        </div>

                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={cs(styles.label, styles.require)}>
                        <span>昵称</span>
                    </div>
                    <div className={styles.value}>
                        <input type="text" placeholder='请输入' />
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>性别</span>
                    </div>
                    <div className={styles.value}>
                        <Radio.Group>
                            <Radio value={1}>男</Radio>
                            <Radio value={2}>女</Radio>
                        </Radio.Group>
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>签名</span>
                    </div>
                    <div className={styles.value}>
                        <textarea placeholder='请输入' />
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>所属公司</span>
                    </div>
                    <div className={styles.value}>
                        <input type='text' placeholder='请输入' />
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>职业</span>
                    </div>
                    <div className={styles.value}>
                        <input type='text' placeholder='请输入' />
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>电子邮箱</span>
                    </div>
                    <div className={styles.value}>
                        <input type='text' placeholder='请输入' />
                        <Checkbox className={cs(styles.emilAgree, 'ant-small-checkbox')}>接受邮件订阅服务</Checkbox>
                    </div>
                    
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>收货地址</span>
                    </div>
                    <div className={styles.value}>
                        <input type='text' placeholder='请输入' />
                    </div>
                </div>
                <div className={styles.formItem} style={{alignItems: 'flex-start'}}>
                    <div className={styles.label}>
                        <span>关注行业(多选)</span>
                    </div>
                    <div className={styles.value}>
                        <Checkbox.Group options={industryList} />
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>手机号码</span>
                    </div>
                    <div className={styles.value}>
                        <div className={styles.sp}>
                            <span>23423423423423</span>
                            <div className={styles.formBtn} onClick={() => setPhoneVisible(true)}>更换手机号</div>
                        </div>
                    </div>
                </div>
                <div className={styles.formItem}>
                    <div className={styles.label}>
                        <span>登录密码</span>
                    </div>
                    <div className={styles.value}>
                        <div className={styles.sp}>
                            <span>xxxxxxxxx</span>
                            <div className={styles.formBtn} onClick={() => setPwVisible(true)}>修改密码</div>
                        </div>
                    </div>
                </div>

                <div className={styles.confirmBtn}>修改保存</div>
            </div>
            {/* 修改手机号弹窗 */}
            {
                phoneVisible && <PhoneModal visible={phoneVisible} onClose={() => setPhoneVisible(false)} />
            }
            {/* 修改密码弹窗 */}
            {
                pwVisible && <PasswordModal visible={pwVisible} onClose={() => setPwVisible(false)} />
            }
        </div>
    );
}

export default BaseInfo;