

import { iconL } from '@/assets/img';
import styles from './index.module.less';

interface UserMsgApplyProps {
    onClose: () => void;
}


const LevelInfo = (props: UserMsgApplyProps) => {
    const { onClose } = props;

    return (
        <div className={styles.levelInfo}>
            <div className={styles.head}>
                <span>等级说明</span>
                <div className={styles.back} onClick={onClose}>
                    <img src={iconL} alt="" />
                    <span>返回</span>
                </div>
            </div>
            <div className={styles.con}>
                富文本内容
                
            </div>
        </div>
    );
}

export default LevelInfo;