

// import SideMenu from '@/components/sideMenu';
import PostList from './components/postList';
import HomeHotPlate from '../home/components/homeHotPlate';
import PostSearchBlock from './components/postSearchBlock';
import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
// import ThemeDesc from './components/themeDesc';
import styles from './index.module.less';

const PostCenter = () => {
    // 即使帖子列表页（首页帖子列表点击更多时跳转）；也是主题板块页（点击侧边side跳转。多了一块描述ThemeDesc）
    // 还是搜索结果页（首页点击搜索，本页右边搜索。隐藏侧边SideMenu，显示找到几条搜索的描述）
    const crumbList: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home'
        },
        {
            name: '帖子列表' // 或者是二级主题名；或者搜索结果
        }
    ];

    return (
        <div className={styles.postCenter}>
            {/* <SideMenu /> */}
            <div className={styles.center}>
                <BreadCrumb list={crumbList}  />
                <span className={styles.searchDesc}>为您找到5个关于“访问控制”的搜索结果</span>
                {/* <ThemeDesc /> */}
                <PostList />
            </div>
            <div>
                <PostSearchBlock />
                <HomeHotPlate />
            </div>
        </div>
    );
}

export default PostCenter;