// src/contexts/GlobalStateProvider.tsx
import { useReducer, useContext } from 'react';
import GlobalContext, { initialState, GlobalState, Action } from './globalContext';

// 定义 reducer 函数
function reducer(state: GlobalState, action: Action): GlobalState {
  switch (action.type) {
    case 'ADD':
      return { ...state, count: state.count + 1 };
    case 'CHANGECOUNT':
      return { ...state, count: action.value };
    // ...其他 action 处理
    default:
      return state;
  }
}

// 定义全局状态提供者组件
export const GlobalStateProvider: React.FC<React.PropsWithChildren> = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <GlobalContext.Provider value={{ state, dispatch }}>
      {children}
    </GlobalContext.Provider>
  );
};

// 优化输出使用方式，及异步处理
export interface IeditStore {
  getData: () => void;
}
export const useStore = (): [GlobalState, IeditStore] => {
  const { state, dispatch } = useContext(GlobalContext)
  const getData = async () => {
      await new Promise<void>((resolve) => {
          setTimeout(() => {
              dispatch({type: 'CHANGECOUNT', value: Math.random()})
              resolve();
          }, 1000);
      })
  }
  return [state, {
      getData,
  }]
}