// import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { renderRoutes } from 'react-router-config';
import routes from './router/index';
import { GlobalStateProvider } from '@/store/globalHook';

function App() {
  return (
    <GlobalStateProvider>
      <Router>
        {renderRoutes(routes)}
      </Router>
    </GlobalStateProvider>
    
  )
}

export default App
