

import { iconShot1, iconShot2, iconShot3 } from '@/assets/img';
import styles from './index.module.less';


const InfoList = () => {
    return (
        <div className={styles.infoList}>
            <ul>
                <li>
                    <div className={styles.title}>
                        <img src={iconShot1} alt="" />
                        <span>擅长领域</span>
                    </div>
                    <div className={styles.desc}>EMC、PROFIBUS通信、PROFINET通信、PLC通信、硬件、STEP7和TIA博途软件以及编程等领域</div>
                </li>
                <li>
                    <div className={styles.title}>
                        <img src={iconShot2} alt="" />
                        <span>技术背景</span>
                    </div>
                    <div className={styles.desc}>拥有20多年的自动化系统经验，是自动化技术权威和领军人物</div>
                </li>
                <li>
                    <div className={styles.title}>
                        <img src={iconShot3} alt="" />
                        <span>作者作品</span>
                    </div>
                    <div className={styles.desc}>• 《工业网络通信指南》上下册 • 《S7可编程控制器-STEP7 编程指南》 • 《TIA 博途软件-STEP7 V11编程指南》• 《TIA 博途与S7-1500编程指南》</div>
                </li>
            </ul>
        </div>
    )
}

export default InfoList;