

import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import PostUserCard from './components/postUserCard';
import HomeHotPlate from '../home/components/homeHotPlate';
import More from '@/components/more';
import { iconUp, iconComment, iconEye, iconLiked } from '@/assets/img';
import styles from './index.module.less';

const PostDetail = () => {
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '帖子详情'
        }
    ];
    return (
        <div className={styles.postDetail}>
            <BreadCrumb list={crumb} />
            <div className={styles.main}>
                <div className={styles.detailWrap}>
                    {/* detail */}
                    <div className={styles.detail}>
                        <div className={styles.titleWrap}>
                            <div className={styles.l}>
                                <span className={styles.icon}>精华</span>
                                <span className={styles.title}>中控仪表天然气门站监控管理系统简介</span>
                            </div>
                            <img className={styles.iconUp} src={iconUp} alt="" />
                        </div>
                        <div className={styles.actionWrap}>
                            <ul className={styles.l}>
                                <li>2022-05-03 15:56</li>
                                <li>
                                    <span>1268</span>
                                    <img src={iconEye} alt="" />
                                </li>
                                <li>
                                    <span>455</span>
                                    <img src={iconComment} alt="" />
                                </li>
                                <li>
                                    <span>55</span>
                                    <img className='cursor' src={iconLiked} alt="" />
                                </li>
                            </ul>
                            <span className={styles.r}>举报</span>
                        </div>
                        <span className={styles.type}>#变频器</span>
                        <div>富文本内容</div>
                    </div>
                    {/* 评论 */}
                    <div className={styles.comment}>
                        <div className={styles.titleWrap}>
                            <span>评论</span>
                            <More title='论坛规范' />
                        </div>
                        
                    </div>
                </div>
                
                <div>
                    <PostUserCard />
                    <HomeHotPlate />
                </div>
            </div>
        </div>
    );
}

export default PostDetail;