import { useState } from 'react';

import { iconAdd } from '@/assets/img';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';


const MyFollow = () => {
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);
    return (
        <>
            <div className={styles.myFollow}>
                <div className={styles.item}>
                    <img className={styles.avatar} src="" alt="" />
                    <div className={styles.c}>
                        <p className={styles.username}>张三三</p>
                        <p className={styles.desc}>现任F轻系统架构师，10年的Net领域经验，欢迎留言！</p>
                    </div>
                    <div className={styles.btn1}>
                        <img src={iconAdd} alt="" />
                        <span>关注</span>
                    </div>
                    <div className={styles.btn2}>已关注</div>
                    <div className={styles.btn2}>互相关注</div>
                </div>
            </div>
            <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
        </>
        
    );
}

export default MyFollow;