import { iconLevel2 } from '@/assets/img';


const Level = () => {
    // 根据等级显示不同的icon
    return (
        <>
            <img style={{height: 20 / 19.2 + 'vw', verticalAlign: 'middle'}} src={iconLevel2} alt="" />
        </>
    );
}

export default Level;