

import cs from 'classnames';
import styles from './index.module.less';


const SystemMsg = () => {
    return (
        <div className={cs(styles.systemMsg, styles.unread)}>
            <p className={styles.title}>由于您发布的内容含有敏感词汇，已被下架，请按平台规范进行发帖与评论。</p>
            <p className={styles.date}>2022-09-09  15:25</p>
        </div>
    );
}

export default SystemMsg;