import { useState } from 'react';

import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import Tabs, {TabsProps} from '@/components/tabs';
import InfoList from './components/infoList';
import VideoList from './components/videoList';
// import ArticleList from './components/articleList';
import styles from './index.module.less';


const BigShots = () => {
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '社区',
            route: '/home',
        },
        {
            name: '大咖主页'
        }
    ];
    const tabs: TabsProps['options'] = [
        {
            label: '视频',
            value: 1,
        },
        {
            label: '文章',
            value: 2,
        },
    ];
    const [current, setCurrent] = useState<TabsProps['current']>(tabs[0].value);
    return (
        <>
            <div className={styles.baseInfo}>
                <img className={styles.avatar} src="" alt="" />
                <div>
                    <div className={styles.lt}>
                        <p className={styles.username}>张三三</p>
                        <div className={styles.shenfen}>全球高级核心专家</div>
                    </div>
                    <ul className={styles.lm}>
                        <li>
                            <span className={styles.label}>关注</span>
                            <span className={styles.value}>120</span>
                        </li>
                        <li>
                            <span className={styles.label}>浏览</span>
                            <span className={styles.value}>1999</span>
                        </li>
                        <li>
                            <span className={styles.label}>作品</span>
                            <span className={styles.value}>8</span>
                        </li>
                    </ul>
                    <div className={styles.lb}>
                        <div className={styles.follow}>+ 关注</div>
                    </div>
                </div>
            </div>
            <div className={styles.content}>
                <BreadCrumb list={crumb} />
                <div className={styles.main}>
                    <div className={styles.l}>
                        <InfoList />
                    </div>
                    <div className={styles.r}>
                        <div className={styles.rInner}>
                            <Tabs options={tabs} current={current} onChange={setCurrent} />
                            <div>
                                {
                                    current === 1 && <VideoList />
                                }
                                {
                                    current === 2 && <VideoList />
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
        </>
    )
}

export default BigShots;